import Vue from 'vue'
import Vuex from 'vuex'

//Vuex
Vue.use(Vuex)

//VueX
const store = new Vuex.Store({
    state:{
        //
        leftDrawStatus:false,

    },
    mutations:{
       
        changeLeftDrawStatus(state){
 
            state.leftDrawStatus = true
        },
        hideLeftDrawStatus(state){
 
            state.leftDrawStatus = false
        },

    }
})

export default store