<template>
  <div>
    <van-popup
      v-model="$store.state.leftDrawStatus"
      position="left"
      style="width: 50%; padding-top: 50px; height: 100%; z-index: 10000"
    >
      <van-cell title="" is-link />
      <van-cell title="" is-link @click="hideLeftDrawStatus('/analysis')"  />
      <van-cell title="" is-link @click="hideLeftDrawStatus('/dataCenter')" to="/dataCenter" />
      <van-cell title="" is-link @click="hideLeftDrawStatus('/change_password')" to="/change_password" />
      <van-cell title="" is-link @click="hideLeftDrawStatus('/changeInfo')" to="/changeInfo" />
      <van-cell title="" is-link  @click="logout" />
    </van-popup>
  </div>
</template>

<style scoped>
</style>

<script>
import { Popup, Cell } from "vant";
import localStorage from "@utils/store/localStorage";
export default {
  name: "Sizebar",
  props: {},
  components: {
    [Cell.name]: Cell,
    [Popup.name]: Popup,
  },
  data: function () {
    return {
      show: true,
    };
  },
  created() {},
  methods: {
    hideLeftDrawStatus(e) {
 
      this.$store.commit("hideLeftDrawStatus");

      this.$router.replace({ path: e });
    },

    logout() {
      localStorage.remove("ntoken");
      this.$router.replace({ path: "/" });
    },
  },
};
</script>